@import '../../../public/variables';

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 100px;
}
.list {
  position: sticky;
  list-style: none;
  margin: 0px;
  padding: 0px;
}
.contacts {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  right: 2%;
  top: 50%;
}
.text-contact {
  text-transform: uppercase;
  font-size: 16px;
}
